.preview-content__wrap {
  display: grid;
  grid-template-columns: repeat(2, 813px);
  column-gap: 135px;
  padding-top: 48px;
  padding-bottom: 29px;
}

.preview-content__img {
  width: 100%;
  object-fit: cover;
}

.preview-content__wrap h2 {
  margin-bottom: 38px;
}

.preview-content__wrap h3 {
  font-size: 75px;
  line-height: 104px;
  margin-bottom: 38px;
}

.preview-content__wrap h4 {
  font-size: 67px;
  line-height: 72px;
}

.preview-content__wrap p {
  margin-bottom: 25px;
}

.preview-content__item  {
  margin-bottom: 69px;
}
