.site-nav {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  column-gap: 42px;
  row-gap: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

.site-nav li {
  position: relative;
  font-family: inherit;
  font-size: 23px;
  line-height: 28px;
  font-weight: 700;
  font-variant: small-caps;
  letter-spacing: -0.5px;
}

.site-nav a {
  color: #000000;
  text-decoration: none;
}

.site-nav li::before,
.site-nav li:last-child::after {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  top: 43%;
  background-color: #a46c2d;
  border-radius: 50%;
}

.site-nav li::before {
  left: -22px;
}

.site-nav li:last-child::after {
  right: -22px;
}

.site-nav__item--active a {
  color: #a46c2d;
}
