.life-slider {
  position: relative;
  width: 100%;
}

.life-slider__list {
  width: 100%;
  height: 445px;
}

.life-slider .swiper-container {
  @media (max-width: 580px) {
    height: 320px;
  }
}

.life-slider__item .swiper-slide {
  display: flex;
  max-width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.life-slider__item img {
  object-fit: cover;
}

.life-slider__item {
  display: block;
}

.life-slider__list p {
  margin-top: 34px;
  margin-bottom: 0;
}

.life-slider__title {


}

.life-slider__toggle {
  position: absolute;
  width: 50px;
  height: 37px;
  bottom: 0px;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 10;

  @media (max-width: 767px) {
    display: none;
  }
}

.life-slider__toggle--prev {
  right: 80px;
  background-image: url("../img/arrow-slider.png");
  transform: scaleX(-1);
}

.life-slider__toggle--next {
  right: 0;
  background-image: url("../img/arrow-slider.png");
}

.life-slider__toggle:active {
  opacity: 0.5;
}
