.page-footer__right,
.page-footer__right a {
  font-family: inherit;
  font-size: 21px;
  text-align: right;
  padding-top: 52px;
  padding-right: 160px;
  padding-bottom: 46px;

  @media (max-width: 992px) {
    text-align: center;
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.page-footer__right a {
  padding: 0;

  @media (max-width: 580px) {
    display: inline-block;
  }
}
