.business {
  background-color: #373736;
}

.business__list.container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 150px;
  row-gap: 60px;
  padding-top: 69px;
  padding-bottom: 67px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    text-align: center;
  }

  @media (max-width: 767px) {
    row-gap: 40px;
  }

  @media (max-width: 480px) {
    row-gap: 30px;
  }
}

.business__list p,
.business__list a {
  font-size: 21px;
  color: #ffffff;
  margin: 0;

  @media (max-width: 1280px) {
    font-size: 20px;
  }
}

.business__list p {
  margin-bottom: 35px;

  @media (max-width: 1280px) {
    margin-bottom: 20px;
  }
}

p.business__copyright {
  font-family: inherit;
  font-size: 21px;
  margin-bottom: 28px;

  @media (max-width: 1280px) {
    margin-bottom: 37px;
  }
}

.business__item:nth-child(3) {
  padding-left: 7.4vw;

  @media (max-width: 1280px) {
    padding-left: 0;
  }
}

a.business__tel {
  text-decoration: none;
}

.business__info a {
  font-weight: 100;
}

.business__form {
  margin-top: 37px;
}
