.sidenav {

}

.sidenav li {
  border-bottom: 2px solid #cecece;
}

.sidenav li:last-child {
  border-bottom: none;
}

.sidenav li:first-child a {
  padding-top: 0;
}

.sidenav a {
  display: block;
  font-family: inherit;
  font-size: 21px;
  line-height: 27px;
  font-weight: 700;
  color: #2b2a29;
  text-transform: uppercase;
  letter-spacing: -1px;
  text-decoration: none;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 23px;
  padding-bottom: 23px;
  margin: 0;
}

a.sidenav__link-active {
  font-weight: 700;
  color: #a46c2d;
}
