.container {
  max-width: 1920px;
  padding-left: 3.65vw;
  padding-right: 4vw;
  margin: 0 auto;

  @media (max-width: 1600px) {
    max-width: 1600px;
    padding-left: 3.5vw;
    padding-right: 3.5vw;
  }

  @media (max-width: 1280px) {
    max-width: 1280px;
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }

  @media (max-width: 992px) {
    max-width: 992px;
    padding-left: 4vw;
    padding-right: 4vw;
  }

  @media (max-width: 767px) {
    max-width: none;
    padding-left: 2.6vw;
    padding-right: 2.6vw;
  }
}
