.content-inner {
  position: relative;
}

.content-inner h1 {
  line-height: 80px;
}

.content-inner h1 > span {
  display: block;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 34px;
  font-weight: 700;
  letter-spacing: -0.5px;
  color: #373736;
  font-variant: normal;
  margin-top: 50px;
}

.content-inner__subtitle {
  font-family: inherit;
  font-size: 34px;
  font-weight: 300;
  color: #a46c2d;
  margin: 0;
  margin-top: 40px;
  margin-bottom: 45px;
}

.content-inner p {
  margin-bottom: 35px;
}

.content-inner__marker-list {
  list-style-type: disc;
  list-style-position: inside;
  margin-bottom: 35px;
}

.content-inner__number-list {
  list-style-position: inside;
  padding-left: 0;
}

.content-inner__list {
  margin-top: 55px;
}

.content-inner__list li {
  margin-bottom: 55px;
}

.content-inner__list p {
  font-size: 34px;
  line-height: 38px;
  letter-spacing: -1.5px;
  margin: 0;
  margin-bottom: 47px;
}

.content-inner__list span,
.content-inner__list a {
  display: block;
  font-size: 25px;
  color: #336699;
  padding-left: 90px;
  margin-bottom: 30px;
}

.content-inner__list span::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 38px;
  left: 28px;
  background-image: url("../img/star.svg");
  background-repeat: no-repeat;
}

.content-inner--questions .content-inner__list span::before {
  width: 36px;
  height: 36px;
  background-image: url("../img/list.svg");
}

.content-inner__list a::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  left: 28px;
  background-image: url("../img/pdf.svg");
  background-repeat: no-repeat;
}
