.navigation {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  padding-top: 33px;
  padding-left: 160px;
  padding-right: 160px;
}

.navigation__contact {
  margin-left: auto;
}

.navigation__contact {
  font-family: "Minion-Pro", "Times", serif;
  font-size: 42px;
  font-weight: 700;
  color: #decc9e;
  text-decoration: none;
}

.navigation__contact span {
  display: block;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 25px;
  font-variant: small-caps;
}
